import React, { useState, useEffect } from "react";

export default function RiskAmoutQuantity({diff}){
    const [riskAmt, setRiskAmt] = useState("");

    const handleChange = (event) => {
        setRiskAmt(event.target.value.replace(/\D/,''));
        // if (searchTermR.length == 0) {
        //     return;
        // }
        // searchStock();
      };

    return(
        <div style={{display:"flex"}}>
        <input
            style={{width:"70px"}}
            type="text"
            placeholder="Risk"
            value={riskAmt}
            onChange={handleChange}
            // onKeyDown={handleKeypress}
        />
        <div style={{margin: 10}}>
            {Math.round(riskAmt/diff)}
        </div>
        </div>
    );
}